
@charset "UTF-8";
/*
 * 基础色
 */
.wrap {
  display: inline-block;
  position: relative;
}
.label {
  position: absolute;
  top: 50%;
  left: 0px;
  font-size: 14px;
  -webkit-transform: translate3d(0, -50%, 0);
          transform: translate3d(0, -50%, 0);
  color: #868686;
  transition: all .3s;
}
.label-shrink {
    font-size: 12px;
    top: -15px;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
.input {
  width: 100%;
  position: relative;
  padding: 8px 0;
  color: #383838;
  background-color: transparent;
  border: none;
  outline: none;
  width: 230px;
  border-bottom: 2px solid #383838;
}
.input:-webkit-autofill {
    -webkit-text-fill-color: #fff;
    -webkit-box-shadow: 0 0 0px 1000px #fff inset;
}
.progress {
  height: 2px;
  width: 0;
  position: absolute;
  bottom: 0;
  left: 0;
  background-color: #ff4f3e;
  transition: width .3s;
}
.progress.error {
    background-color: #ff4f3e;
}
.progress.success {
    background-color: #75c940;
}



